<template>
  <div class="map-wrap" id="map-wrap">
    <div id="map"></div>
  </div>
</template>
<script>
export default {
  methods: {
    initmap() {
      const container = document.getElementById('map'); //ÏßÄÎèÑÎ•º Îã¥ÏùÑ ÏòÅÏó≠Ïùò DOM Î†àÌçºÎü∞Ïä§
      const options = {
        //ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï† Îïå ÌïÑÏöîÌïú Í∏∞Î≥∏ ÏòµÏÖò
        center: new window.kakao.maps.LatLng(
          37.653302895848235,
          126.89929384702089
        ), //ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú.
        level: 2, //ÏßÄÎèÑÏùò Î†àÎ≤®(ÌôïÎåÄ, Ï∂ïÏÜå Ï†ïÎèÑ)
      };

      const map = new window.kakao.maps.Map(container, options); //ÏßÄÎèÑ ÏÉùÏÑ± Î∞è Í∞ùÏ≤¥ Î¶¨ÌÑ¥

      const markerPosition = new window.kakao.maps.LatLng(
        37.652970895848235,
        126.89919384702089
      );

      // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      const marker = new window.kakao.maps.Marker({
        position: markerPosition,
      });

      // ÎßàÏª§Í∞Ä ÏßÄÎèÑ ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ïÌï©ÎãàÎã§
      marker.setMap(map);

      var iwContent =
          '<div class="kakao-wrap"><span>Îã¨ÌïòÎØºÎîîÏ†ÄÌä∏</span> <div class="kakao-bottom">    <span>ÌòÑÎåÄÌó§Î¶¨ÏóáÏÉÅÍ∞Ä 101Îèô 148Ìò∏</span> <a href="https://map.kakao.com/?itemId=964454959">Í∏∏Ï∞æÍ∏∞</a> </div></div>',
        iwRemoveable = false; // removeable ÏÜçÏÑ±ÏùÑ ture Î°ú ÏÑ§Ï†ïÌïòÎ©¥ Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º Îã´ÏùÑ Ïàò ÏûàÎäî xÎ≤ÑÌäºÏù¥ ÌëúÏãúÎê©ÎãàÎã§

      // Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      var infowindow = new window.kakao.maps.InfoWindow({
        content: iwContent,
        removable: iwRemoveable,
      });

      // ÎßàÏª§Ïóê ÌÅ¥Î¶≠Ïù¥Î≤§Ìä∏Î•º Îì±Î°ùÌï©ÎãàÎã§
      window.kakao.maps.event.addListener(marker, 'click', function () {
        // ÎßàÏª§ ÏúÑÏóê Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º ÌëúÏãúÌï©ÎãàÎã§
        infowindow.open(map, marker);
      });
    },
  },
  mounted() {
    // if (window.kakao && window.kakao.maps) {
    //   this.initmap();
    // } else {
    const script = document.createElement('script');
    script.src =
      'http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=0b7c92a6c73aa46580d8d6f3439004c8';
    script.onload = () => window.kakao.maps.load(this.initmap);

    document.head.appendChild(script);
  },
  //   },
};
</script>

